<template>
	<div class="am-g ta-g">
		<div class="am-u-md-8">
			<form class="am-form am-form-horizontal" id="merchant-form" autocomplete="off" onsubmit="return false;">
				<fieldset>
					<div class="am-form-group">
						<label>账户名（手机号码）</label>
						<input type="tel" name="mobile" v-model="mobile" :disabled="mobileDisabled ? true: false" required maxlength="11" required pattern="^\d{11}$">
					</div>
					<div class="am-form-group">
						<label>登陆密码</label>
						<input type="password" id="merchant-pw" v-model="password1" name="password1" required maxlength="18" pattern="^\w{6,18}$" placeholder="6-18 位密码" data-validation-message="请输入6-18 位密码">
					</div>
					<div class="am-form-group">
						<label>确认密码</label>
						<input type="password" name="password2" v-model="password2" data-equal-to="#merchant-pw" required maxlength="18" placeholder="再次输入密码" data-validation-message="请输入6-18 位密码">
					</div>
					<div class="am-form-group">
						<label>商户名称</label>
						<input type="text" name="name" v-model="name" required minlength="1" maxlength="15" pattern="^[\u4e00-\u9fa5]{1,15}$" placeholder="例：美游人生" data-validation-message="请输入中文">
					</div>
					<button type="submit" class="am-btn am-btn-primary">确定</button>
				</fieldset>
			</form>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'MerchantAdd',
		data () {
			return {
				mobileDisabled: false,
				mobile: '',
				password1: '',
				password2: '',
				name: '',
				time: ''
			}
		},
		methods: {
			submit() {
				var self = this;
				layer.confirm('确定新增?', {
					title: '系统提示',
					btn: ['新增', '取消']
				}, function() {
					layer.closeAll()
					self.time = $.getTime();
					console.log(JSON.stringify(self.$data))
					self.$router.replace('/merchant/list')
				})
			}
		},
		mounted () {
			const self = this;
			$('#merchant-form').validator({
				onValid: function(validity) {
					$(validity.field).closest('.am-form-group').find('.am-alert').hide();
				},
				onInValid: function(validity) {
					let $field = $(validity.field);
					let $group = $field.closest('.am-form-group');
					let $alert = $group.find('.am-alert');
					// 使用自定义的提示信息 或 插件内置的提示信息
					let msg = $field.data('validationMessage') || this.getValidationMessage(validity);
					if(!$alert.length) {
						$alert = $('<div class="am-alert am-alert-danger"></div>').hide().
						appendTo($group);
					}
					$alert.html(msg).show();
				},
				submit: function(e, validity) {
					var formValidity = this.isFormValid();
					if(formValidity) {
						self.submit()
					} ;
					return false;
				}
			});
		}
	}
</script>


