<template>
	<div class="am-g am-cf">
		<div class="am-u-sm-12 am-padding-0">

			<div class="am-panel ta-panel">
				<div class="am-panel-hd am-cf">
					<div class="am-input-group am-u-md-5 am-padding-left-0" style="max-width: 350px;">
						<input type="text" class="am-form-field" v-model="queryData" placeholder="例：商家名">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button" @click="query">
								查询
							</button>
							<router-link to="/merchant/add" tag="button" class="am-btn am-btn-default" type="button">
								新增
							</router-link>
						</span>
					</div>
				</div>
				<div class="am-panel-bd">
					<table class="am-table am-table-striped am-table-bordered am-table-hover ta-table am-margin-0">
						<thead>
							<tr>
								<th style="width: 40px;">
									<input type="checkbox" class="ta-table-check-all" v-model="allCheckbox">
								</th>
								<th>商家名称</th>
								<th>手机号码</th>
								<th class="am-hide-sm-only">入驻时间</th>
								<th style="max-width: 120px;">操作</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(tData, index) in tableData">
								<template v-if="index < rowSelected">
									<td><input type="checkbox" class="ta-td-checkbox" v-model="tData.checked"></td>
									<td>
										<a href="javscript:;" @click="openPurchase(tData.name)">
											{{tData.name}}
										</a>
									</td>
									<td>{{ tData.mobile }}</td>
									<td class="am-hide-sm-only">{{ tData.time }}</td>
									<td>
										<div class="am-dropdown ta-sm-show" data-am-dropdown>
											<button class="am-btn am-btn-success am-dropdown-toggle am-padding-xs"> 
												<span class="am-icon-cog"></span>
											</button>
											<ul class="am-dropdown-content ta-dropdown-btn-content am-list">
												<li>
													<a href="javascript:;" @click="edit(index)">
														<span class="am-text-primary">
															<i class="am-icon-edit"></i> 编辑
														</span>
													</a>
												</li>
												<li>
													<a href="javascript:;" @click="remove(index)">
														<span class="am-text-danger">
															<i class="am-icon-remove"></i> 删除
														</span>
													</a>
												</li>
											</ul>
										</div>
										
										<div class="ta-sm-hide">
											<a href="javascript:;" class="am-text-primary" @click="edit(index)">
												<i class="am-icon-edit"></i> 编辑
											</a>
											<a href="javascript:;" class="am-text-danger" @click="remove(index)">
												<i class="am-icon-remove"></i> 删除
											</a>
										</div>
									</td>
								</template>
							</tr>
						</tbody>
					</table>
				</div>
				<div class="am-panel-footer">
					<div class="am-cf">
						<div class="ta-table-settings am-u-md-4 am-padding-left-0">
							<a href="javascript:;" class="am-btn am-btn-success am-btn-sm" @click="checkedAll()">全选</a>
							<a href="javascript:;" class="am-btn am-btn-warning am-btn-sm" @click="versa">反选</a>
							<a href="javascript:;" class="am-btn am-btn-danger am-btn-sm"  @click="removeAll">删除</a>
						</div>
						<ul class="am-pagination ta-pagination am-margin-vertical-0 am-pagination-right am-u-md-8">
							<li class="ta-sm-hide">
								<a href="javascript:;">共<span class="am-text-danger" v-text="total"></span>条</a>
							</li>
							<li class="ta-sm-hide">
								<a href="javascript:;" v-on:click="home">首页</a>
							</li>
							<li v-if="curr > 1">
								<a href="javascript:;" v-on:click="prev">上一页</a>
							</li>
							<li v-if="curr < pages">
								<a href="javascript:;" v-on:click="next">下一页</a>
							</li>
							<li>
								<a href="javascript:;">共<span class="am-text-danger" v-text="pages"></span>页</a>
							</li>
							<li class="ta-pagination-rowTotal">
								<a class="ta-select-page" href="javascript:;">
									<select v-model="rowSelected" v-cloak>
										<option v-for="row in rowTotal" v-bind:value="row">{{row}}</option>
									</select>
								</a>
							</li>
						</ul>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: 'MerchantList',
		data() {
			return {
				pages: 0,
				total: 0,
				curr: 1,
				rowTotal: [10, 20, 50, 100],
				rowSelected: 10,
				tableData: [],
				queryData: '',
				allCheckbox: ''
			}
		},
		created() {
			// 获取数据
			const self = this;
			const data = self.getData(4)
			self.tableData = data;
			self.total = data.length;
			self.pages = Math.ceil(self.total / self.rowSelected); //得到总页数
		},
		watch: {
			allCheckbox (nw, ow) {
				const self = this;
				if(nw == 'versa' || ow == 'versa'){
					self.allCheckbox = false;
				}else{
					for(let i=0; i < self.rowSelected; i++){
						let tData = self.tableData[i];
						if(tData){
							tData.checked = self.allCheckbox;
						}
					}
				}
			}
		},
		methods: {
			getData(size) {
				const data = [];
				for(let i = 0; i < size; i++) {
					data.push({
						checked: false,
						name: '商家-' + i,
						mobile: '1370137' + $.getRandom(4),
						time: $.getTime()
					})
				}
				return data;
			},
			openPurchase (id) {
				// 查看当前商家的已消费列表
				this.$router.push({
					path: '/purchase',
					query: {id: id}
				})
			},
			next() {
				this.curr += 1;
			},
			prev() {
				this.curr -= 1;
			},
			home() {
				this.curr = 1;
			},
			query() {
				console.log(this.queryData)
			},
			checkedAll () {
				const self = this;
				self.allCheckbox = !self.allCheckbox;
			},
			versa () {
				const self = this;
				self.allCheckbox = 'versa';
				for(let i=0; i < self.rowSelected; i++){
					let tData = self.tableData[i];
					if(tData){
						tData.checked = !tData.checked;
					}
				}
			},
			removeAll () {
				layer.msg('删除选中')
			},
			edit(index) {
				layer.msg('编辑' + index)
			},
			remove(index) {
				var self = this;
				layer.confirm('确定删除?', {
					title: '系统提示',
					btn: ['删除', '取消']
				}, function() {
					layer.closeAll()
					self.tableData.splice(index, 1)
				})

			}
		},
		mounted () {
			$('.am-dropdown').dropdown()
		}
	}
</script>


