import * as types from './mutation-types'

export default {
	[types.LOGIN](state, user) {
		state.Login.status = true
		state.Login.userName = user.userName
	},
	[types.SETLOGO](state, logo) {
		state.Logo = logo
	},
	[types.CHECKUNFOLD](state) {
		if(state.UnfoldStatus !== undefined && !state.UnfoldStatus) {
			state.UnfoldStatus = true
		}
		state.Unfold = !state.Unfold
	},
	[types.FOLD](state) {
		// 屏幕小于 1025时出现遮罩
		if(state.Unfold && document.body.clientWidth < 1025) {
			state.Unfold = false
		}
	},
	[types.CHECKTHEME](state, theme) {
		localStorage.TATHEME = theme
		state.Settings.theme = theme
	},
	[types.STORAGEFOLD](state, checked) {
		if(checked){
			state.Unfold = false
		}
		localStorage.SIDEBARFOLD = checked
	},
	[types.ADDTOAST](state, toast) {
		if(typeof toast == 'string'){
			state.Toast.push({
				txt: toast
			})
		}else{
			state.Toast.push(toast)
		}
	},
	[types.REMOVETOAST](state, index) {
		state.Toast.splice(index, 1)
	}
};


