<template>
	<div id="app">
		<div v-if="Config.Login.status" class="ta-app" :class="[Config.Unfold ? 'ta-unfold' : 'ta-fold', 'ta-theme-'+Config.Settings.theme]">
			<my-head></my-head>

			<div class="ta-content">

				<my-sidebar></my-sidebar>
				<div class="ta-overlay" :class="{'active': Config.Unfold}" @click="fold" @touchstart="fold"></div>

				<section class="ta-content-wrapper">
					<div class="am-cf ta-router-header">
						<a href="javascript:;" class="am-btn am-btn-md am-fl ta-router-prev" @click="historyPrev">
							<i class="iconfont icon-fanhui"></i> 返回上一页
						</a>
						<div v-if="routeName" class="ta-router-title am-text-truncate am-secondary">
							<button class="am-btn">{{routeName}}</button>
						</div>
					</div>
					<router-view></router-view>
				</section>
			</div>

			<my-toast></my-toast>
			<my-settings></my-settings>
		</div>

		<template v-else>
			<!-- 未登陆路由 -->
			<router-view></router-view>
		</template>
	</div>

</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'

	import MyHead from 'components/Head'
	import MySidebar from 'components/Sidebar'
	import MySettings from 'components/Settings'
	import MyToast from 'components/Toast'
	import MyLogin from 'components/Login'

	export default {
		name: 'App',
		data () {
			return {
				routeName: ''
			}
		},
		computed: {
			...mapGetters({
				Config: 'getConfig'
			})
		},
		created () {
			this.routeName = this.$route.name
		},
		watch: {
			'$route' (to, from) {//监听路由改变
				this.routeName = to.name
			}
		},
		components: {
			MyHead,
			MySidebar,
			MySettings,
			MyToast,
			MyLogin
		},
		methods: {
			...mapActions({
				fold: 'Fold'
			}),
			historyPrev() {
				this.$router.go(-1)
			}
		}
	}
</script>

<style lang="less">
	@import "assets/css/ta.less";
	@import "assets/css/main.less";
</style>


