<template>
	<div class="ta-app login-container">
		<div class="login-bg">
			<img src="static/images/login-bg.jpg"/>
		</div>
		<div class="login-box">
			<!--<div class="login-logo-img">
				<img src="static/images/logo.png" alt="" />
			</div>-->
			<div class="login-title">
				<h2>短信激活码管理系统</h2>
				<p>打造一个OTO简洁的短信激活平台</p>
			</div>
			<form class="am-form" id="login-form" onsubmit="return false;">
				<div class="am-form-group">
					<label><i class="am-icon-user"></i></label>
					<input type="text" name="userName" value="admin" maxlength="18" placeholder="输入账户" required />
				</div>

				<div class="am-form-group">
					<label><i class="am-icon-key"></i></label>
					<input type="password" name="password" value="admin" placeholder="输入密码" required />
				</div>
				<button class="am-btn am-btn-secondary" type="submit">登录</button>
			</form>
		</div>
	</div>
</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'

	export default {
		name: 'Login',
		data() {
			return {
				name: '1',
				text: '2'
			}
		},
		computed: {
			...mapGetters({
				Login: 'getLogin'
			})
		},
		methods: {
			...mapActions({
				login: 'login'
			}),
			submit() {
				this.login({
					userName: 'admin',
					password: 'admin'
				})
				this.$router.push(this.$store.getters.getSidebar[0].link)
			}
		},
		mounted() {
			const self = this;
			const $form = $('#login-form');
			$form.find('input').on('focus', function() {
				let thisElm = this;
				if($.getSystem() != 'PC') { // 解决移动端遮盖输入框事件
					$(window).one('resize', function() {
						thisElm.scrollIntoView()
					})
				}
			})

			$form.validator({
				submit: function(e, validity) {
					var formValidity = this.isFormValid();
					if(formValidity) {
						self.submit()
					}
					return false;
				}
			})
		}
	}
</script>

<style lang="less" scoped>
	.login-container {
		font-family: "Lucida Grande","Microsoft YaHei",Verdana,Lucida,Arial,Helvetica,sans-serif;
		width: 100%;
		transition: all .3s ease-in;
		.login-bg{
			position: absolute;
			width: 100%;
			height: 100%;
			z-index: -1;
			img{
				height: 100%;
				width: 100%;
			}
		}
		.login-box {
			width: 326px;
			margin: 150px auto 0;
			.login-title {
				text-align: center;
				h2{
				    font-weight: bold;
				}
			}
			.login-logo-img {
				width: 150px;
				margin: 0 auto;
			}
			.am-form {
				margin-top: 50px;
				.am-form-group {
					overflow: auto;
					border-bottom: 2px #68c6de solid;
					margin-bottom: 30px;
					label {
						float: left;
						i {
							font-size: 24px;
							color: #68c6de
						}
					}
					input {
						width: 265px;
						float: left;
						margin-left: 10px;
						border: 0;
						background: 0 0
					}
				}
				.am-btn-secondary {
					width: 100px;
					height: 3rem;
					background: #68c6de;
					border-color: #68c6de;
					border-radius: 27px;
					display: block;
					margin: 0 auto;
				}
			}
		}
	}
	
	@media screen and (max-width:640px) {
		.login-container {
			.login-box {
				padding: 0 20px;
				width: 100%;
				margin-top: 100px;
				.am-form {
					margin-top: 30px;
					.am-form-group input {
						width: 70%
					}
				}
			}
		}
	}
</style>


