<template>
	<transition-group name="toast" class="ta-toast" tag="div" @before-enter="beforeEnter" @before-leave="beforeLeave">
		<div v-for="(toast, index) in Toast" class="am-alert animated" :class="'am-alert-'+ toast.status" :key="toast" :data-index="index" :data-an-in="toast.anIn" :data-an-out="toast.anOut" :data-timeout="toast.timeout" @click="close(index)">
			<button type="button" class="am-close">×</button>
			<p class="ta-alert-title"> {{ toast.txt }} </p>
		</div>
	</transition-group>
</template>

<script>
	import { mapGetters,mapActions } from 'vuex'
	export default {
		name: 'Toast',
		computed: {
			...mapGetters({
				Toast: 'getToast'
			})
		},
		methods: {
			...mapActions({
				removeToast: 'removeToast'
			}),
			beforeEnter(el) {
				const self = this;
				var timeout = 3e3;
				el.classList.add(el.dataset.anIn || 'fadeIn')
				if(el.dataset.timeout){
					timeout = el.dataset.timeout
				}
				el.dataset.setTimeout = setTimeout(function() {
					self.close(el.dataset.index)
				}, timeout);
			},
			beforeLeave(el) {
				clearTimeout(el.dataset.setTimeout)
				el.classList.remove(el.dataset.anIn || 'fadeIn')
				el.classList.add(el.dataset.anOut || 'fadeOut')
			},
			close(index) {
				this.removeToast(index)
			}
		}
	}
</script>


