<template>
	<div class="am-g ta-g">
		<div class="am-u-md-8">

			<form class="am-form am-form-horizontal" id="favorable-form" autocomplete="off" onsubmit="return false;">
				<fieldset>
					<a class="am-text-sm" href="/static/template.xlsx" download="格式模板">
						格式模板下载
					</a>
					<div class="am-form-group">
						<p>
							<span class="am-form-help" style="display: inline-block;">请选择要上传的文件</span><span>（1M以下）</span>
						</p>
						<button class="am-btn am-btn-warning import-user-btn">
							上传 Excel
							<input type="file" name="import" id="import-user" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" single/>
						</button>
						<span>{{ fileName }}</span>
					</div>
					<button type="submit" class="am-btn am-btn-secondary" @click="importFile">导入</button>
					<button type="submit" class="am-btn am-btn-secondary" @click="importFile('send')">导入并发送优惠码</button>
				</fieldset>
			</form>
			
			<h1>上传后的表格需要后台处理文件后才能显示</h1>
		</div>

	</div>
</template>

<script>
	export default {
		components: {},
		name: 'MerchantAdd',
		data() {
			return {
				drop: false,
				fileName: ''
			}
		},
		created() {
			var self = this;
			if($.getSystem() == 'PC') {
				self.drop = true;
			}
		},
		methods: {
			importFile (method) {
				var newFormData = new FormData(document.querySelector('#favorable-form'));
				if(newFormData.get('import').size) {
					if(method == 'send'){
						newFormData.append('method', 'send')
						layer.msg('上传文件：《'+newFormData.get('import').name+'》并发送短信')
					}else{
						layer.msg('上传文件：'+newFormData.get('import').name)
					}
				} else {
					document.querySelector('#import-user').click()
					layer.msg('请先上传文件')
				}
			}
		},
		mounted() {
			const self = this;
			document.querySelector('#import-user').addEventListener('change', function() {
				var _this = this;
				if(_this.files.length) {
					var file = _this.files[0];
					if(file.size > (1024 * 1024)) {
						layer.msg('上传文件过大，请重新上传')
						_this.value = '';
					} else {
						self.fileName = file.name;
					}
				}
			})
		}
	}
</script>

<style type="text/css" scoped>
	.import-user-btn {
		position: relative;
	}
	
	#import-user {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
	}
</style>


