<template>
	<aside class="ta-sidebar">
		<!-- 菜单 -->
		<ul class="ta-sidebar-nav">
			<li class="ta-sidebar-nav-link" v-for="nav in Sidebar">
				<template v-if="!nav.child">
					<a v-if="nav.blank" target="_blank" :href="nav.link">
						<i class="ta-sidebar-nav-link-logo" :class="nav.icon"></i> {{ nav.name }}
					</a>
					<a v-else href="javascript:;" :class="{ 'am-active': nav.active }" @click="navClick(nav.link)">
						<i class="ta-sidebar-nav-link-logo" :class="nav.icon"></i> {{ nav.name }}
					</a>
				</template>
				<template v-else>
					<a class="ta-sidebar-nav-sub-title" :class="{ 'am-active': nav.active }" @click="sidebarClick(nav)">
						<i class="ta-sidebar-nav-link-logo" :class="nav.icon"></i> {{ nav.name }}
						<span class="am-icon-chevron-right am-fr am-margin-right-sm ta-sidebar-nav-sub-ico"></span>
					</a>
					<ul class="ta-sidebar-nav ta-sidebar-nav-sub" :class="{'am-show': nav.active}">
						<li class="ta-sidebar-nav-link" v-for="child in nav.child">
							<a href="javascript:;" :class="{ 'am-active': child.active }" @click="navClick(child.link)">
								<i class="ta-sidebar-nav-link-logo" :class="child.icon"></i> {{ child.name }}
							</a>
						</li>
					</ul>
				</template>
			</li>
		</ul>

	</aside>
</template>

<script>
	import { mapGetters, mapActions } from 'vuex'
	
	export default {
		name: 'Sidebar',
		computed: {
			...mapGetters({
				Sidebar: 'getSidebar'
			})
		},
		methods: {
			...mapActions({
				fold: 'Fold'
			}),
			sidebarClick(nav) {
				this.Sidebar.forEach(function(nav) {
					if(nav.child){ // 收起其他二级菜单
						nav.active = false;
					}
				})
				nav.active = !nav.active;	// 切换active
			},
			navClick (vlink) {
				this.fold();
				this.$router.push(vlink)
			}
		}
	}
</script>


