import Vue from 'vue'
import router from './router'
import store from './vuex/store'
import App from './App'

require('assets/js/jquery.ta')

FastClick.attach(document.body)

// 路由钩子
router.beforeEach((to, from, next) => {
	$.AMUI.progress.start();
	if(store.getters.getLogin.status) { // 已登陆状态
		let path = to.path.split('/')[1]; // 截取一级路由
		if(!path) { // 如果不存在一级路由或者在登陆页面就跳转到首页
			router.replace('/home')
		}else {
			path = '/' + path;
			// 切换导航
			store.getters.getSidebar.forEach(function(nav) {
				nav.active = false;
				if(nav.link == path) {
					nav.active = true;
				}
				if(nav.child) {
					nav.child.forEach(function(child) {
						child.active = false;
						if(child.link == to.path) {
							nav.active = true;
							child.active = true;
						}
					})
				}
			})
		}
	} else {
		if(to.path != '/login') {
			// TODO 手动后退浏览器历史记录时，此代码虽然执行了，但达不到效果
			router.replace('/login')
		}
	}
	next()
})

router.afterEach((to, from) => {
	let loginStatus = store.getters.getLogin.status;
	let path = to.path;
	// 如果已经登陆又跳到登陆页面就做重定向
	if(loginStatus && path == '/login') {
		layer.msg('您已经登陆了')
		router.replace(from.path)
	}
	
	if(!loginStatus && path != '/login') {
		// 手动后退浏览器历史记录时如果又没登陆又不是登陆页就重定向
		router.replace('/login')
	}
	$.AMUI.progress.done();
})

new Vue({
	router,
	store,
	el: '#app',
	template: '<App/>',
	components: {
		App
	},
	created() {
		const self = this;
		let path = self.$route.path.split('/')[1];
		if(path) {
			// 始终返回一级路由
			self.$router.replace('/' + path)
		}
		$.AMUI.progress.start()
	},
	mounted() {
		// 页面加载完毕，清除 load
		setTimeout(function() {
			$.AMUI.progress.done(true);
			$('.ta-loader').addClass('animated fadeOut').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function() {
				$(this).remove();
			});
		}, 50);
	}
}).$mount('#app')


