<template>
	<!-- 头部 -->
	<header class="ta-header">
		<!-- logo -->
		<div class="am-fl ta-header-logo">
			<a href="javascript:;">
				<i class="iconfont icon-yanzhengma"></i>
				<span class="ta-header-logo-name"> {{ logo }}</span>
			</a>
		</div>

		<!-- 右侧内容 -->
		<div class="ta-header-fluid">
			<!-- 侧边切换 am-icon-list -->
			<div class="am-fl ta-header-switch-button">
				<a href="javascript:void(0)" role="button" class="ta-menubar-toggle-btn hamburger hamburger--arrow" :class="{'is-active': unfold }" @click="hamburger">
					<span class="hamburger-box">
						<span class="hamburger-inner"></span>
					</span>
				</a>
			</div>

			<!-- 搜索 -->
			<div class="am-fl ta-header-search">
				<form class="ta-header-search-form" action="javascript:;">
					<button class="ta-header-search-btn am-icon-search"></button>
					<input class="ta-header-search-box" type="text" placeholder="搜索内容...">
				</form>
			</div>

			<!-- 其它功能-->
			<div class="am-fr ta-header-navbar" @click="navbarClick">
				<ul>
					<!-- 用户 -->
					<li class="am-dropdown head-dropdown">
						<a href="javascript:;" class="am-dropdown-toggle">
							<i class="iconfont icon-yonghu icon-max"></i>
							<span>{{ login.userName }}</span>
						</a>
						<!-- 弹出列表 -->
						<ul class="am-dropdown-content ta-dropdown-content">
							<li>
								<a href="javascript:;">
									<i class="iconfont icon-xiugaimima"></i>
									<span class="am-padding-left-xs">资料</span>
								</a>
							</li>
							<li>
								<a href="javascript:;" @click="cancel">
									<i class="iconfont icon-zhuxiao"></i>
									<span class="am-padding-left-xs">注销</span>
								</a>
							</li>
						</ul>
					</li>
				</ul>
			</div>
		</div>
	</header>
</template>

<script>
	import {mapGetters, mapActions } from 'vuex'	
	export default {
		name: 'Head',
		computed: {
			...mapGetters({
				login: 'getLogin',
				logo: 'getLogo',
				unfold: 'getUnfold'
			})
		},
		methods: {
			...mapActions({
				fold: 'Fold',
				checkUnfold: 'checkUnfold'
			}),
			hamburger() {
				this.checkUnfold()
			},
			navbarClick() {
				this.fold()
			},
			cancel () {
				this.login.status = false;
				this.$router.replace('/login')
			}
		},
		mounted () {
			$('header li.head-dropdown').dropdown()
		}
	}
</script>



