<template>
	<div class="am-g ta-g">
		<div class="am-u-md-8">
			<form class="am-form am-form-horizontal" id="config-form" autocomplete="off" onsubmit="return false;">
				<fieldset>
					<div class="am-form-group">
						<label>网站标题</label>
						<input type="text" name="WEB_TITLE" maxlength="10" :value="configData.WEB_TITLE" placeholder="例如：优惠码系统">
					</div>
					<div class="am-form-group">
						<label>网站关键词</label>
						<input type="text" name="WEB_KEYWORDS" maxlength="18" :value="configData.WEB_KEYWORDS" placeholder="例如：优惠码，优惠码系统">
					</div>
					<div class="am-form-group">
						<label>网站描述</label>
						<textarea class="web-description" name="WEB_DESCRIPTION" maxlength="100">{{configData.WEB_DESCRIPTION}}</textarea>
					</div>
					<!--<div class="am-form-group">
						<label>网站备案</label>
						<input type="text" name="WEB_ICP" value="京ICP备110号">
					</div>
					<div class="am-form-group">
						<label>客服QQ</label>
						<input type="text" class="js-pattern-number" name="WEB_QQ" value="465040621" pattern="[1-9][0-9]{4,14}" maxlength="14" data-validation-message="QQ必须为 4-14 位数字">
					</div>-->
					<button type="submit" class="am-btn am-btn-primary">保存</button>
				</fieldset>
			</form>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'ConfigView',
		data () {
			return {
				configData: {}
			}
		},
		created() {
			var self = this;
			$.ajax({
				url: "config"
			}).done(function(data) {
				if(typeof data == 'string'){
					data = JSON.parse(data)
				}
				self.configData = data;
			})
		},
		mounted() {
			var self = this;
			$('#config-form').validator({
				submit: function(e, validity) {
					var formValidity = this.isFormValid();
					var $thisForm = $(e.target);
					if(formValidity) { // 验证通过
						updateMetaData()
					}
				}
			});
		}
	}
</script>
<style type="text/css" scoped>
	.web-description {
		min-height: 100px;
		max-height: 250px;
	}
</style>


