import Vue from 'vue'
import VueRouter from 'vue-router'

import NotFound from 'components/NotFound'

/* 登陆页面 */
import LoginView from 'components/Login'

/* 系统配置页面 */
import Home from '../view/home/view'
/* 商家管理页面 */
import MerchantView from '../view/merchant/view'
import MerchantList from '../view/merchant/list'
import MerchantAdd from '../view/merchant/add'
/* 优惠码管理页面 */
import FavorableView from '../view/favorable/view'
import FavorableList from '../view/favorable/list'
import FavorableAdd from '../view/favorable/add'
/* 已消费列表页面 */
import PurchaseList from '../view/purchase/list'

Vue.use(VueRouter)

const router = new VueRouter({
//	mode: 'history',
	routes: [{
		path: '/',
		component: Home
	}, {
		path: '/login',
		component: LoginView
	}, {
		path: '/home',
		name: '系统配置',
		component: Home
	}, {
		path: '/merchant',
		component: MerchantView,
		children: [{
			path: '/',
			name: '商家管理',
			component: MerchantList
		}, {
			path: 'list',
			component: MerchantList
		}, {
			path: 'add',
			name: '新增商家',
			component: MerchantAdd
		}]
	}, {
		path: '/favorable',
		component: FavorableView,
		children: [{
			path: '/',
			name: '优惠码管理',
			component: FavorableList
		}, {
			path: 'list',
			component: FavorableList
		}, {
			path: 'add',
			name: '新增优惠码',
			component: FavorableAdd
		}]
	}, {
		path: '/purchase',
		name: '已消费列表',
		component: PurchaseList
	}, {
		path: '*',
		component: NotFound
	}]
})

export default router


