import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'
import mutations from './mutations'
Vue.use(Vuex)

// 应用初始状态
const state = {
	Login: {
		status: true,
		userName: ''
	},
	Logo: '激活码系统',
	Sidebar: [{
		name: '系统配置',
		link: '/home',
		active: true,
		icon: 'iconfont icon-xitongpeizhi'
	}, {
		name: '商家管理',
		link: '/merchant',
		active: false,
		icon: 'iconfont icon-shangjia1'
	}, {
		name: '号码管理',
		link: '/favorable',
		active: false,
		icon: 'iconfont icon-youhui'
	}, {
		name: '已消费列表',
		link: '/purchase',
		active: false,
		icon: 'iconfont icon-youhui'
	}, {
		name: '验证消费',
		blank: true,
		link: 'consume.html',
		active: false,
		icon: 'iconfont icon-youhui'
	}],
	Settings: {
		theme: 'black',
		themes: ['white', 'black']
	},
	Unfold: true,
	UnfoldStatus: false,
	Toast: []
}

// 检测存储值
if(typeof localStorage.SIDEBARFOLD !== 'undefined'){
	let sf = eval(localStorage.SIDEBARFOLD);
	state.Unfold = !sf
	if(sf){ // 如果为收起左侧栏，默认就不开启适配屏幕了
		state.UnfoldStatus = sf
	}
}
if(typeof localStorage.TATHEME !== 'undefined'){
	state.Settings.theme = localStorage.TATHEME
}

// 适配屏幕宽度，判断是否隐藏侧导航
function watchResize(width) {
	if(state.UnfoldStatus) { // 如果手动展开过，就不去做屏幕适配
		return false;
	}
	if(width < 1024) {
		state.Unfold = false
	} else {
		state.Unfold = true
	}
}

if(('addEventListener' in document)) {
	document.addEventListener('DOMContentLoaded', function() {
		window.addEventListener('resize', function() {
			watchResize(document.body.clientWidth)
		})
	}, false);
}

if(state.Unfold) {
	watchResize(document.body.clientWidth + 17)
}

// 创建 store 实例
export default new Vuex.Store({
	actions,
	getters,
	state,
	mutations
})


