<template>
	<div class="am-g am-cf">
		<div class="am-u-sm-12 am-padding-0">

			<div class="am-panel ta-panel">
				<div class="am-panel-hd am-cf">
					<div class="am-input-group am-u-md-6 am-u-lg-4">
						<input type="text" class="am-form-field" v-model="queryData" placeholder="例：激活码编号">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button" @click="query">
								查询
							</button>
							<router-link to="/favorable/add" tag="button" class="am-btn am-btn-default" type="button">
								新增
							</router-link>
						</span>
					</div>

					<div class="am-input-group am-u-md-6 am-u-lg-4">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button">
								优惠码状态
							</button>
						</span>
						<select class="ta-panel-hd-select">
							<option value="all" selected>所有</option>
							<option value="1">未发送</option>
							<option value="2">未消费</option>
							<option value="3">已作废</option>
							<option value="4">已消费</option>
						</select>
					</div>

				</div>
				<div class="am-panel-bd">
					<table class="am-table am-table-striped am-table-bordered am-table-hover ta-table am-margin-0">
						<thead>
							<tr>
								<th>激活码</th>
								<th>状态</th>
								<th>手机号码</th>
								<th class="am-hide-sm-only">导入时间</th>
								<th class="am-hide-sm-only">发送时间</th>
								<th style="max-width: 120px;">操作</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(tData, index) in tableData">
								<template v-if="index < rowSelected">
									<td>{{ tData.cdkey }}</td>
									<td>
										<span v-if="tData.status == 1" class="am-text-secondary">未发送</span>
										<span v-if="tData.status == 2" class="am-text-warning">未消费</span>
										<span v-if="tData.status == 3" class="am-text-danger">已作废</span>
										<span v-if="tData.status == 4" class="am-text-success">已消费</span>
									</td>
									<td>{{ tData.mobile }}</td>
									<td class="am-hide-sm-only">{{ tData.sendTime }}</td>
									<td class="am-hide-sm-only">
										<span class="am-btn am-btn-secondary am-btn-xs" v-if="tData.status == 1" @click="sendMsg(index)">
											发送激活码
										</span>
										<span v-else>{{ tData.time }}</span>
									</td>
									<td>
										<div class="am-dropdown ta-sm-show" data-am-dropdown>
											<button class="am-btn am-btn-success am-dropdown-toggle am-padding-xs"> 
												<span class="am-icon-cog"></span>
											</button>
											<ul class="am-dropdown-content ta-dropdown-btn-content am-list">
												<li v-if="tData.status == 1">
													<a href="javascript:;" @click="sendMsg(index)">
														<span class="am-text-success">
															<i class="am-icon-envelope-o"></i> 发送激活码
														</span>
													</a>
												</li>
												<li v-if="tData.status == 2">
													<a href="javascript:;" @click="invalid(index)">
														<span class="am-text-warning">
															<i class="am-icon-ban"></i> 作废
														</span>
													</a>
												</li>
												<li>
													<a href="javascript:;" @click="edit(index)">
														<span class="am-text-primary">
															<i class="am-icon-edit"></i> 编辑
														</span>
													</a>
												</li>
												<li>
													<a href="javascript:;" @click="remove(index)">
														<span class="am-text-danger">
															<i class="am-icon-remove"></i> 删除
														</span>
													</a>
												</li>
											</ul>
										</div>
										
										<div class="ta-sm-hide">
											<a href="javascript:;" class="am-text-warning" @click="invalid(index)">
												<i class="am-icon-ban"></i> 作废
											</a>
											<a href="javascript:;" class="am-text-primary" @click="edit(index)">
												<i class="am-icon-edit"></i> 编辑
											</a>
											<a href="javascript:;" class="am-text-danger" @click="remove(index)">
												<i class="am-icon-remove"></i> 删除
											</a>
										</div>
									</td>
								</template>
							</tr>
						</tbody>
					</table>
				</div>
				<div class="am-panel-footer">
					<div class="am-cf">
						<ul class="am-pagination ta-pagination am-margin-vertical-0 am-pagination-right">
							<li class="ta-sm-hide">
								<a href="javascript:;">共<span class="am-text-danger" v-text="total"></span>条</a>
							</li>
							<li class="ta-sm-hide">
								<a href="javascript:;" v-on:click="home">首页</a>
							</li>
							<li v-if="curr > 1">
								<a href="javascript:;" v-on:click="prev">上一页</a>
							</li>
							<li v-if="curr < pages">
								<a href="javascript:;" v-on:click="next">下一页</a>
							</li>
							<li>
								<a href="javascript:;">共<span class="am-text-danger" v-text="pages"></span>页</a>
							</li>
							<li class="ta-pagination-rowTotal">
								<a class="ta-select-page" href="javascript:;">
									<select v-model="rowSelected" v-cloak>
										<option v-for="row in rowTotal" v-bind:value="row">{{row}}</option>
									</select>
								</a>
							</li>
						</ul>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: 'MerchantList',
		data() {
			return {
				pages: 0, // 总页码
				total: 0, // 总条数
				curr: 1, // 当前页码
				rowTotal: [10, 20, 50, 100], // 每页行数
				rowSelected: 10, // 下拉框 selected 值
				tableData: [], // 数据
				queryData: '', // 查询数据
			}
		},
		created() {
			// 获取数据
			const self = this;
			const data = self.getData(21)
			self.tableData = data;
			self.total = data.length;
			self.pages = Math.ceil(self.total / self.rowSelected); //得到总页数
		},
		methods: {
			getData(size) {
				const data = [];
				for(let i = 0; i < size; i++) {
					let randomNumber = $.getRandom(6);
					data.push({
						checked: false,
						cdkey: randomNumber,
						mobile: '13701' + randomNumber,
						status: $.getRandom(1, 4),
						sendTime: $.getTime({
							type: "before",
							timeFormat: "yyyy-MM-dd hh:mm"
						}),
						time: $.getTime({
							timeFormat: "yyyy-MM-dd hh:mm"
						})
					})
				}
				return data;
			},
			next() {
				this.curr += 1;
			},
			prev() {
				this.curr -= 1;
			},
			home() {
				this.curr = 1;
			},
			query() {
				const queryData = this.queryData;
				if(queryData.trim()) {
					layer.msg('查询数据：' + queryData)
				}
			},
			sendMsg(index) {
				layer.msg('发送优惠码：' + index)
			},
			invalid (index) {
				layer.msg('作废：' + index)
			},
			edit (index) {
				layer.msg('编辑：' + index)
			},
			remove(index) {
				var self = this;
				layer.confirm('确定删除?', {
					title: '系统提示',
					btn: ['删除', '取消']
				}, function() {
					layer.closeAll()
					self.tableData.splice(index, 1)
				})

			}
		},
		mounted () {
			$('.am-dropdown').dropdown()
		}
	}
</script>


