<template>
	<!-- 风格切换 -->
	<div class="ta-settings" :class="{'active': active}">
		<div class="ta-settings-toggle" @click="toggle">
			<i class="am-icon-cog am-icon-spin"></i>
		</div>
		<div class="ta-settings-content">
			<div>
				<span class="ta-settings-title">收起侧边栏：</span>
				<label class="am-checkbox am-secondary ta-settings-checkbox-for">
					<input type="checkbox" id="ta-settings-unfold-sidebar" >
				</label>
			</div>

			<hr class="ta-settings-content-hr" />

			<div class="am-cf">
				<span v-for="theme in Settings.themes" class="ta-settings-theme" :class="['ta-settings-theme-'+theme]" @click="toggleTheme(theme)"></span>
			</div>
		</div>
	</div>

</template>

<script>
	import { mapGetters, mapActions } from 'vuex'
	export default {
		name: 'Settings',
		computed: {
			...mapGetters({
				Settings: 'getSettings'
			})
		},
		data() {
			return {
				active: false
			}
		},
		methods: {
			...mapActions({
				fold: 'Fold',
				storageFold: 'storageFold',
				checkTheme: 'checkTheme'
			}),
			toggle() { // 切换设置窗口
				this.fold()
				this.active = !this.active
			},
			toggleTheme(theme) { // 切换主题
				this.checkTheme(theme)
			}
		},
		mounted () {
			var self = this;
			var $unfoldCheckBox = $('#ta-settings-unfold-sidebar');
			
			if(typeof localStorage.SIDEBARFOLD !== 'undefined'){
				$unfoldCheckBox.prop('checked', eval(localStorage.SIDEBARFOLD));
			}
			
			$unfoldCheckBox.on('change', function() {
				self.storageFold(this.checked)
			});
		}
	}
</script>


