<template>
	<div class="am-g am-cf">
		<div class="am-u-sm-12 am-padding-0">

			<div class="am-panel ta-panel">
				<div class="am-panel-hd am-cf">
					<div class="am-input-group am-u-md-6 am-u-lg-4">
						<input type="text" class="am-form-field" v-model="queryData" placeholder="例：激活码编号">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button" @click="query">
								查询
							</button>
						</span>
					</div>

					<div class="am-input-group am-u-md-6 am-u-lg-4">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button">
								选择商家
							</button>
						</span>
						<select class="ta-panel-hd-select" v-model="merchant">
							<option v-for="(mData, index) in merchants" :value="mData.id">
								{{mData.name}}
							</option>
						</select>
					</div>

				</div>
				<div class="am-panel-bd">
					<table class="am-table am-table-striped am-table-bordered am-table-hover ta-table am-margin-0">
						<thead>
							<tr>
								<th>激活码</th>
								<th>服务项</th>
								<th>消费手机</th>
								<th class="am-hide-sm-only">消费时间</th>
								<th class="am-hide-sm-only">消费商家</th>
								<th style="max-width: 120px;">操作</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(tData, index) in tableData">
								<template v-if="index < rowSelected">
									<td>{{ tData.cdkey }}</td>
									<td>{{ tData.service }}</td>
									<td>{{ tData.mobile }}</td>
									<td class="am-hide-sm-only">{{ tData.time }}</td>
									<td class="am-hide-sm-only">{{ tData.merchant }}</td>
									<td>
										<div class="am-dropdown ta-sm-show" data-am-dropdown>
											<button class="am-btn am-btn-success am-dropdown-toggle am-padding-xs"> 
												<span class="am-icon-cog"></span>
											</button>
											<ul class="am-dropdown-content ta-dropdown-btn-content am-list">
												<li>
													<a href="javascript:;" @click="edit(index)">
														<span class="am-text-primary">
															<i class="am-icon-edit"></i> 编辑
														</span>
													</a>
												</li>
												<li>
													<a href="javascript:;" @click="remove(index)">
														<span class="am-text-danger">
															<i class="am-icon-remove"></i> 删除
														</span>
													</a>
												</li>
											</ul>
										</div>
										
										<div class="ta-sm-hide">
											<a href="javascript:;" class="am-text-primary" @click="edit(index)">
												<i class="am-icon-edit"></i> 编辑
											</a>
											<a href="javascript:;" class="am-text-danger" @click="remove(index)">
												<i class="am-icon-remove"></i> 删除
											</a>
										</div>
									</td>
								</template>
							</tr>
						</tbody>
					</table>
				</div>
				<div class="am-panel-footer">
					<div class="am-cf">
						<ul class="am-pagination ta-pagination am-margin-vertical-0 am-pagination-right">
							<li class="ta-sm-hide">
								<a href="javascript:;">共<span class="am-text-danger" v-text="total"></span>条</a>
							</li>
							<li class="ta-sm-hide">
								<a href="javascript:;" v-on:click="home">首页</a>
							</li>
							<li v-if="curr > 1">
								<a href="javascript:;" v-on:click="prev">上一页</a>
							</li>
							<li v-if="curr < pages">
								<a href="javascript:;" v-on:click="next">下一页</a>
							</li>
							<li>
								<a href="javascript:;">共<span class="am-text-danger" v-text="pages"></span>页</a>
							</li>
							<li class="ta-pagination-rowTotal">
								<a class="ta-select-page" href="javascript:;">
									<select v-model="rowSelected" v-cloak>
										<option v-for="row in rowTotal" v-bind:value="row">{{row}}</option>
									</select>
								</a>
							</li>
						</ul>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: 'MerchantList',
		data() {
			return {
				merchant: '商家-0', // 接收商家参数
				merchants: [{
					name: '商家-0',
					id: '商家-0'
				}, {
					name: '商家-1',
					id: '商家-1'
				}, {
					name: '商家-2',
					id: '商家-2'
				}, {
					name: '商家-3',
					id: '商家-3'
				}],
				pages: 0, // 总页码
				total: 0, // 总条数
				curr: 1, // 当前页码
				rowTotal: [10, 20, 50, 100], // 每页行数
				rowSelected: 10, // 下拉框 selected 值
				tableData: [], // 数据
				queryData: '' // 查询数据
			}
		},
		created() {
			// 获取数据
			const self = this;
			if(self.$route.query.id) { // 获取其他页面的查询参数
				self.merchant = self.$route.query.id
			}
			self.getData(21, self.merchant);
		},
		watch: {
			'merchant' () {
				this.getData($.getRandom(2), this.merchant);
			}
		},
		methods: {
			getData(size, id) {
				const self = this;
				const data = [];
				for(let i = 0; i < size; i++) {
					let randomNumber = $.getRandom(6);
					data.push({
						checked: false,
						cdkey: randomNumber,
						mobile: '186102' + randomNumber,
						service: '开房',
						time: $.getTime(),
						merchant: self.merchant || '商家-' + $.getRandom(1)
					})
				}
				self.tableData = data;
				self.total = data.length;
				self.pages = Math.ceil(self.total / self.rowSelected); //得到总页数
			},
			next() {
				this.curr += 1;
			},
			prev() {
				this.curr -= 1;
			},
			home() {
				this.curr = 1;
			},
			query() {
				const queryData = this.queryData;
				if(queryData.trim()) {
					layer.msg('查询数据：' + queryData)
				}
			},
			edit(index) {
				layer.msg('编辑：' + index)
			},
			remove(index) {
				var self = this;
				layer.confirm('确定删除?', {
					title: '系统提示',
					btn: ['删除', '取消']
				}, function() {
					layer.closeAll()
					self.tableData.splice(index, 1)
				})

			}
		},
		mounted () {
			$('.am-dropdown').dropdown()
		}
	}
</script>


